.side-drawer {
    height:100%;
    background-color: wheat;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    left:0;
    width:70%;
    max-width: 345px;
    z-index:101;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}

.side-drawer.open {
    transform: translateX(0);
}

.side-drawer ul {
    height:100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer ul a {
    text-decoration: none;
    color:navy;
    font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: darkorange;
}

@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}